/* Clearing floats */
.cf:before,
.cf:after {
    content: " ";
    display: table;
}

.cf:after {
    clear: both;
}

.cf {
    // *zoom: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Mini reset, no margins, paddings or bullets */
.menu,
.submenu {
    position: relative;
    z-index: 15;
    margin: 0;
    padding: 0;
    list-style: none;
    transition: 0.5s;
}

/* Main level */
.menu {

    transition: 0.5s;
    margin: 0 0 10px 0;
    width: 100%;
    /* http://www.red-team-design.com/horizontal-centering-using-css-fit-content-value */
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
}

.menu>li {
    background: #007732;
    float: left;
    position: relative;
    // transform: skewX(25deg);
    transition: 0.3s;
    border: none;
    text-align: center;
}

.menu a {
    transition: 0.5s;
    display: block;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
}

.menu li:hover {
    background: #e74c3c;
}

.menu>li>a {
    // transform: skewX(-25deg);
    padding: 1em 2em;
}

/* Dropdown */
.submenu {
    transition: 0.5s;
    position: absolute;
    z-index: 20;
    width: 200px;
    left: 50%;
    margin-left: -100px;
    // transform: skewX(-25deg);
    transform-origin: left top;
}

.submenu li {
    background-color: #007732;
    position: relative;
    overflow: hidden;
    text-align: center;
}

.submenu>li>a {
    padding: 1em 2em;
}

.submenu>li::after {
    content: '';
    position: absolute;
    top: -125%;
    height: 100%;
    width: 100%;
    box-shadow: 0 0 50px rgba(0, 0, 0, .9);
}

/* Odd stuff */
.submenu>li:nth-child(odd) {
    // transform: skewX(-25deg) translateX(0);
}

.submenu>li:nth-child(odd)>a {
    transform: skewX(25deg);
}

.submenu>li:nth-child(odd)::after {
    right: -50%;
    transform: skewX(-25deg) rotate(3deg);
}

/* Even stuff */
.submenu>li:nth-child(even) {
    transform: skewX(25deg) translateX(0);
}

.submenu>li:nth-child(even)>a {
    transform: skewX(-25deg);
}

.submenu>li:nth-child(even)::after {
    left: -50%;
    // transform: skewX(25deg) rotate(3deg);
}

/* Show dropdown */
.submenu,
.submenu li {
    opacity: 0;
    visibility: hidden;
}

.submenu li {
    transition: .2s ease transform;
}

.menu>li:hover .submenu,
.menu>li:hover .submenu li {
    opacity: 1;
    visibility: visible;
}

.menu>li:hover .submenu li:nth-child(even) {
    transform: skewX(25deg) translateX(15px);
}

.menu>li:hover .submenu li:nth-child(odd) {
    transform: skewX(-25deg) translateX(-15px);
}



